@import '../../../node_modules/swiper/swiper.scss';
@import '../../../node_modules/swiper/modules/navigation/navigation.scss';
@import '../../../node_modules/swiper/modules/pagination/pagination.scss';
@import './swiper-brands.scss';
@import './swiper-appliances.scss';
@import './swiper-prices.scss';


.swiper {
  &-slide {
    flex-shrink: 1;
  }

  &-pagination {
    display: flex; //    flex-
    justify-content: center; // bullets  

    &-bullet {
      height: 12px; //   bullets  12 px
      width: 12px; //   bullets  12 px
      background: #DDD; //    bullets
      border-radius: 50%; //   
      margin: 0 6px; //      6px

      &-active {
        background: #B5B6BC; //    bullets
      }
    }
  }
}

@media (min-width: 768px) {

  .swiper {

    &-pagination {
      display: none;
    }
  }

}